.portfolio__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2.5rem;
}

.portfolio__container1 {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2.5rem;
}

.portfolio__item {
    background: rgba(102, 126, 234, 0.1);
    backdrop-filter: blur(10px);
    padding: 1.5rem;
    border-radius: 2rem;
    border: 1px solid rgba(77, 181, 255, 0.2);
    transition: var(--transition);
    position: relative;
    overflow: hidden;
}

.portfolio__item::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--gradient-primary);
    opacity: 0;
    transition: var(--transition);
    z-index: -1;
}

.portfolio__item:hover {
    border-color: var(--color-primary);
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(77, 181, 255, 0.2);
}

.portfolio__item:hover::before {
    opacity: 0.05;
}

.portfolio__item-img {
    border-radius: 1.5rem;
    overflow: hidden;
    position: relative;
    aspect-ratio: 16/9;
    margin-bottom: 1rem;
}

.portfolio__item-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: var(--transition);
}

.portfolio__img-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(10, 14, 39, 0.9);
    backdrop-filter: blur(5px);
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: var(--transition);
}

.portfolio__item-img:hover .portfolio__img-overlay {
    opacity: 1;
}

.portfolio__img-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
}

.portfolio__overlay-btn {
    padding: 0.75rem 1.5rem;
    background: var(--gradient-primary);
    color: var(--color-white);
    border-radius: 0.5rem;
    font-weight: 500;
    transition: var(--transition);
    transform: translateY(10px);
    opacity: 0;
}

.portfolio__item-img:hover .portfolio__overlay-btn {
    transform: translateY(0);
    opacity: 1;
    transition-delay: 0.1s;
}

.portfolio__overlay-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 25px rgba(77, 181, 255, 0.4);
}

.portfolio__item-img:hover img {
    transform: scale(1.1);
}

.portfolio__tech-badges {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin: 1rem 0;
}

.tech-badge {
    padding: 0.4rem 0.8rem;
    background: rgba(77, 181, 255, 0.1);
    border: 1px solid rgba(77, 181, 255, 0.3);
    border-radius: 1rem;
    font-size: 0.75rem;
    color: var(--color-primary);
    font-weight: 500;
    transition: var(--transition);
}

.tech-badge:hover {
    background: rgba(77, 181, 255, 0.2);
    border-color: var(--color-primary);
    transform: translateY(-2px);
}

.portfolio__item-language {
    color: grey;
    display: none;
}

.portfolio__item-desc {
    margin-bottom: 1.2rem;
    color: var(--color-light);
    line-height: 1.6;
}

.portfolio__read-more {
    background: none;
    border: none;
    color: var(--color-primary);
    cursor: pointer;
    font-weight: 500;
    padding: 0;
    margin-left: 0.3rem;
    transition: var(--transition);
}

.portfolio__read-more:hover {
    color: var(--color-white);
    text-decoration: underline;
}

.portfolio__item h3 {
    margin: 1rem 0;
    color: var(--color-white);
    font-size: 1.5rem;
}

.portfolio__item-cta {
    display: flex;
    flex-wrap: wrap;
    gap: 1rem;
    margin-top: 1.5rem;
}


/* media query for medium devices */
@media screen and (max-width: 1024px) {
    .portfolio__container {
        gap: 1.2rem;
    }
 
    .portfolio__container1 {
        gap: 1.2rem;
    }

    .portfolio__item {
        padding: 1.2rem;
    }
}
 
/* media query for small devices */
@media screen and (max-width: 600px) {
    .portfolio__container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
 
    .portfolio__container1 {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }
 
    .portfolio__item-img {
        border-radius: 1.5rem;
        overflow: hidden;
        width: 100%;
    }

    .portfolio__item-cta {
        flex-direction: column;
    }

    .portfolio__item-cta .btn {
        width: 100%;
        text-align: center;
    }
}
