.about__container {
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: var(--gradient-primary);
    display: grid;
    place-items: center;
    position: relative;
    overflow: visible;
}

.about__me::before {
    content: '';
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    background: var(--gradient-primary);
    border-radius: 2rem;
    filter: blur(20px);
    opacity: 0.5;
    z-index: -1;
    animation: glowPulse 3s ease-in-out infinite;
}

.about__me-image {
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
    aspect-ratio: 1/1;
    position: relative;
    z-index: 1;
}

.about__me-image:hover {
    transform: rotate(0deg) scale(1.05);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

.about__card {
    background: rgba(102, 126, 234, 0.1);
    backdrop-filter: blur(10px);
    border: 1px solid rgba(77, 181, 255, 0.2);
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
    position: relative;
    overflow: hidden;
}

.about__card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--gradient-primary);
    opacity: 0;
    transition: var(--transition);
    z-index: -1;
}

.about__card:hover {
    border-color: var(--color-primary);
    transform: translateY(-5px);
    box-shadow: 0 10px 25px rgba(77, 181, 255, 0.2);
}

.about__card:hover::before {
    opacity: 0.1;
}

.about__icon {
    color: var(--color-primary);
    font-size: 1.8rem;
    margin-bottom: 1rem;
    transition: var(--transition);
}

.about__card:hover .about__icon {
    transform: scale(1.2) rotate(5deg);
    color: var(--color-white);
}

.about__card h5 {
    font-size: 0.95rem;
    color: var(--color-white);
    margin-bottom: 0.5rem;
}

.about__card small {
    font-size: 0.75rem;
    color: var(--color-light);
}

.about__content p {
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
    line-height: 1.8;
}

@keyframes glowPulse {
    0%, 100% {
        opacity: 0.5;
        transform: scale(1);
    }
    50% {
        opacity: 0.7;
        transform: scale(1.05);
    }
}

/* media query for medium devices */
@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem 0 1.5rem;
    }
}

/* media query for small devices */
@media screen and (max-width: 600px) {
    .about__me {
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .about__content {
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }
}