header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__bg-animation {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 20% 30%, rgba(102, 126, 234, 0.2) 0%, transparent 50%),
                radial-gradient(circle at 80% 70%, rgba(77, 181, 255, 0.2) 0%, transparent 50%);
    animation: pulse 8s ease-in-out infinite;
    z-index: 0;
}

@keyframes pulse {
    0%, 100% {
        opacity: 1;
        transform: scale(1);
    }
    50% {
        opacity: 0.8;
        transform: scale(1.1);
    }
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5rem;
    align-items: center;
    padding-top: 0;
}

.header__text-content {
    position: relative;
    z-index: 10;
    text-align: left;
    padding: 2rem;
}

.header__text-content h5.header__greeting {
    opacity: 1 !important;
    margin-bottom: 1rem;
    font-size: 1.2rem;
    color: var(--color-primary) !important;
    animation: fadeInDown 0.8s ease 0.2s;
    display: block !important;
    visibility: visible !important;
    position: relative;
    z-index: 10;
    background: none !important;
    -webkit-text-fill-color: var(--color-primary) !important;
    text-fill-color: var(--color-primary) !important;
    font-weight: 500;
}

.header__text-content h1.header__name {
    font-size: 3.5rem !important;
    margin: 1rem 0;
    min-height: 4rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 0.3rem;
    opacity: 1 !important;
    visibility: visible !important;
    position: relative;
    z-index: 10;
    color: #ffffff !important;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #4db5ff 100%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 700;
    line-height: 1.2;
}

/* Fallback for browsers that don't support gradient text */
@supports not (background-clip: text) {
    .header__text-content h1.header__name {
        color: #ffffff !important;
        background: none !important;
        -webkit-text-fill-color: #ffffff !important;
    }
}

.cursor {
    display: inline-block;
    width: 3px;
    height: 3rem;
    background: var(--gradient-primary);
    margin-left: 0.2rem;
    animation: blink 1s infinite;
}

@keyframes blink {
    0%, 50% {
        opacity: 1;
    }
    51%, 100% {
        opacity: 0;
    }
}

.header__subtitle {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease, transform 0.8s ease;
    font-size: 1.3rem;
    margin-top: 0.5rem;
}

.header__subtitle.visible {
    opacity: 1;
    transform: translateY(0);
}

.header__cta-wrapper {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease 0.3s, transform 0.8s ease 0.3s;
}

.header__cta-wrapper.visible {
    opacity: 1;
    transform: translateY(0);
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        transform: translateY(-20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* CTA */

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    flex-wrap: wrap;
}

.btn-download {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

.btn-icon {
    font-size: 1.2rem;
}

/* Socials */

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    z-index: 2;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--gradient-primary);
    animation: growLine 1s ease forwards;
}

.header__socials a {
    transition: var(--transition);
    padding: 0.5rem;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header__socials a:hover {
    background: var(--color-bg-variant);
    transform: translateX(5px);
    color: var(--color-primary);
}

@keyframes growLine {
    from {
        height: 0;
    }
    to {
        height: 2rem;
    }
}

/* image */

.me {
    background: var(--gradient-primary);
    width: 22rem;
    height: 29rem;
    position: relative;
    margin: 0 auto;
    border-radius: 12rem 12rem 0 0;
    overflow: visible;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
    animation: float 6s ease-in-out infinite;
    z-index: 1;
}

.me__glow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
    background: var(--gradient-primary);
    border-radius: 12rem 12rem 0 0;
    filter: blur(40px);
    opacity: 0.5;
    z-index: -1;
    animation: glowPulse 3s ease-in-out infinite;
}

.me img {
    position: relative;
    z-index: 1;
    border-radius: 12rem 12rem 0 0;
    transition: transform 0.3s ease;
}

.me:hover img {
    transform: scale(1.05);
}

@keyframes float {
    0%, 100% {
        transform: translateY(0px);
    }
    50% {
        transform: translateY(-20px);
    }
}

.header__subtitle {
    color: var(--color-light) !important;
    font-size: 1.3rem;
    margin-top: 0.5rem;
    text-align: left;
}

.header__image-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.header__text-content .cta {
    justify-content: flex-start;
    margin-top: 2rem;
}

.header__cta-wrapper {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.8s ease 0.3s, transform 0.8s ease 0.3s;
}

.header__cta-wrapper.visible {
    opacity: 1;
    transform: translateY(0);
}

/* Responsive */
@media screen and (max-width: 1024px) {
    .header__container {
        grid-template-columns: 1fr;
        gap: 3rem;
        text-align: center;
    }

    .header__text-content {
        text-align: center;
    }

    .header__text-content h1.header__name {
        justify-content: center;
    }

    .header__text-content .cta {
        justify-content: center;
    }

    .header__subtitle {
        text-align: center;
    }
}

@keyframes glowPulse {
    0%, 100% {
        opacity: 0.5;
        transform: translate(-50%, -50%) scale(1);
    }
    50% {
        opacity: 0.7;
        transform: translate(-50%, -50%) scale(1.1);
    }
}

/* Scroll down */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    transition: var(--transition);
    color: var(--color-primary);
}

.scroll__down:hover {
    color: var(--color-white);
    transform: rotate(90deg) translateX(5px);
}

.scroll__arrow {
    width: 20px;
    height: 20px;
    border-right: 2px solid currentColor;
    border-bottom: 2px solid currentColor;
    transform: rotate(45deg);
    animation: bounceArrow 1.5s ease-in-out infinite;
}

@keyframes bounceArrow {
    0%, 100% {
        transform: rotate(45deg) translateY(0);
    }
    50% {
        transform: rotate(45deg) translateY(-10px);
    }
}

/* media query for medium devices */
@media screen and (max-width: 1024px) {
    header {
        height: 80vh;

    }
}

/* media query for small devices */
@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .header__socials, .scroll__down{
        display: none;
    }
}