.container.contact__container {
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

.contact__options {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.contact__option {
    background: rgba(102, 126, 234, 0.1);
    backdrop-filter: blur(10px);
    padding: 1.5rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid rgba(77, 181, 255, 0.2);
    transition: var(--transition);
}

.contact__option:hover {
    background: rgba(102, 126, 234, 0.15);
    border-color: var(--color-primary);
    transform: translateY(-5px);
    box-shadow: 0 10px 25px rgba(77, 181, 255, 0.2);
}

.contact__option-item {
    font-size: 1.8rem;
    margin-bottom: 0.5rem;
    color: var(--color-primary);
}

.contact__option h4 {
    color: var(--color-white);
    margin-bottom: 0.5rem;
}

.contact__option h5 {
    color: var(--color-light);
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

.contact__option a {
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.85rem;
    color: var(--color-primary);
    transition: var(--transition);
}

.contact__option a:hover {
    color: var(--color-white);
}

/* form */
.contact__form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.form__group {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

input, textarea {
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: rgba(102, 126, 234, 0.1);
    backdrop-filter: blur(10px);
    border: 2px solid rgba(77, 181, 255, 0.2);
    resize: none;
    color: var(--color-white);
    font-family: inherit;
    transition: var(--transition);
}

input:focus, textarea:focus {
    outline: none;
    border-color: var(--color-primary);
    background: rgba(102, 126, 234, 0.15);
    box-shadow: 0 0 0 3px rgba(77, 181, 255, 0.1);
}

input::placeholder, textarea::placeholder {
    color: var(--color-light);
}

input.error, textarea.error {
    border-color: #ff6b6b;
    background: rgba(255, 107, 107, 0.1);
}

.error-message {
    color: #ff6b6b;
    font-size: 0.85rem;
    margin-top: -0.3rem;
}

.form__success {
    padding: 1rem;
    background: rgba(77, 181, 255, 0.1);
    border: 1px solid var(--color-primary);
    border-radius: 0.5rem;
    color: var(--color-primary);
    text-align: center;
    font-weight: 500;
}

.contact__form .btn {
    align-self: flex-start;
    margin-top: 0.5rem;
}

.contact__form .btn:disabled {
    opacity: 0.6;
    cursor: not-allowed;
}

/* media query for medium devices */
@media screen and (max-width: 1024px) {
    .container.contact__container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }

    .contact__options {
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }

    .contact__option {
        flex: 1;
        min-width: 200px;
    }
}

/* media query for small devices */
@media screen and (max-width: 600px) {
    .container.contact__container {
        width: var(--container-width-sm);
        grid-template-columns: 1fr;
    }

    .contact__options {
        flex-direction: column;
    }

    .contact__option {
        width: 100%;
    }

    .contact__form .btn {
        width: 100%;
    }
}