@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #0a0e27;
    --color-bg-variant: rgba(102, 126, 234, 0.1);
    --color-primary: #4db5ff;
    --color-primary-variant: rgba(77, 181, 255, 0.4);
    --color-secondary: #667eea;
    --color-accent: #764ba2;
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;

    --transition: all 400ms ease;
    --transition-slow: all 600ms ease;
    
    --gradient-primary: linear-gradient(135deg, #667eea 0%, #764ba2 50%, #4db5ff 100%);
    --gradient-secondary: linear-gradient(135deg, #4db5ff 0%, #667eea 100%);
    --gradient-bg: radial-gradient(circle at 20% 50%, rgba(102, 126, 234, 0.15) 0%, transparent 50%),
                    radial-gradient(circle at 80% 80%, rgba(77, 181, 255, 0.15) 0%, transparent 50%),
                    radial-gradient(circle at 40% 20%, rgba(118, 75, 162, 0.1) 0%, transparent 50%);
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar{
    display: none;
}

body{
    font-family: "Poppins", sans-serif;
    background: var(--color-bg);
    background-image: var(--gradient-bg);
    color: var(--color-white);
    line-height: 1.7;
    position: relative;
    overflow-x: hidden;
}

body::before {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--gradient-bg);
    z-index: -1;
    animation: gradientShift 15s ease infinite;
    background-size: 200% 200%;
}

@keyframes gradientShift {
    0%, 100% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
}

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5 {
    font-weight: 500;
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

h1 {
    font-size: 2.5rem;
    font-weight: 600;
    letter-spacing: -0.02em;
}

h2 {
    font-size: 2rem;
    font-weight: 600;
    letter-spacing: -0.01em;
}

h3 {
    font-weight: 500;
}

section {
    margin-top: 8rem;
}

.fade-in-up {
    opacity: 0;
    transform: translateY(30px);
    transition: opacity 0.8s ease, transform 0.8s ease;
}

.fade-in-up.visible {
    opacity: 1;
    transform: translateY(0);
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(30px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

section > h2, section > h5 {
    text-align: center;
    color: var(--color-light);
}

section > h2 {
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    margin-bottom: 3rem;
    position: relative;
    display: inline-block;
    width: 100%;
}

section > h2::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 3px;
    background: var(--gradient-primary);
    border-radius: 2px;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover{
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.5rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    position: relative;
    overflow: hidden;
    font-weight: 500;
}

.btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: var(--gradient-primary);
    transition: var(--transition);
    z-index: -1;
}

.btn:hover {
    color: var(--color-white);
    border-color: transparent;
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(77, 181, 255, 0.3);
}

.btn:hover::before {
    left: 0;
}

.btn-primary {
    background: var(--gradient-primary);
    color: var(--color-white);
    border: none;
}

.btn-primary:hover {
    transform: translateY(-2px);
    box-shadow: 0 10px 25px rgba(102, 126, 234, 0.4);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* media query for medium devices */
@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }
    
    section{
        margin-top: 6rem;
    }
}

/* media query for small devices */
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }
    
    section > h2 {
        margin-bottom: 2rem;
    }
}