footer {
    background: var(--gradient-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;
    position: relative;
    overflow: hidden;
}

footer::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at 50% 50%, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
    pointer-events: none;
}

footer a {
    color: var(--color-bg);
    transition: var(--transition);
}

footer a:hover {
    color: var(--color-white);
    transform: translateY(-2px);
}

.footer__logo {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 2rem;
    display: inline-block;
    position: relative;
    z-index: 1;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
    position: relative;
    z-index: 1;
}

.permalinks a {
    transition: var(--transition);
}

.permalinks a:hover {
    text-decoration: underline;
}

.footer__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
    position: relative;
    z-index: 1;
}

.footer__socials a {
    background: var(--color-bg);
    color: var(--color-white);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid transparent;
    transition: var(--transition);
    font-size: 1.2rem;
}

.footer__socials a:hover {
    background: rgba(10, 14, 39, 0.8);
    color: var(--color-white);
    border-color: var(--color-bg);
    transform: translateY(-3px) scale(1.1);
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
}

.footer__copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
    position: relative;
    z-index: 1;
    font-weight: 500;
}

/* media query for small devices */
@media screen and (max-width: 600px) {
    .permalinks{
        flex-direction: column;
        gap: 1.5rem;
    }

    .footer__socials {
        margin-bottom: 2.6rem;
    }
}