.experience__container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 2rem;
}

.experience__card {
    background: rgba(102, 126, 234, 0.1);
    backdrop-filter: blur(10px);
    padding: 2rem;
    border-radius: 2rem;
    border: 1px solid rgba(77, 181, 255, 0.2);
    transition: var(--transition);
    position: relative;
    overflow: hidden;
}

.experience__card::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--gradient-primary);
    opacity: 0;
    transition: var(--transition);
    z-index: -1;
}

.experience__card:hover {
    border-color: var(--color-primary);
    transform: translateY(-5px);
    box-shadow: 0 20px 40px rgba(77, 181, 255, 0.2);
}

.experience__card:hover::before {
    opacity: 0.05;
}

.experience__card h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-white);
    font-size: 1.3rem;
    font-weight: 600;
    background: var(--gradient-primary);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 1.5rem;
}

.experience__details {
    display: flex;
    gap: 1rem;
    align-items: flex-start;
}

.experience__details-icon {
    margin-top: 6px;
    color: var(--color-primary);
    font-size: 1.2rem;
    flex-shrink: 0;
}

.experience__skill-content {
    flex: 1;
    width: 100%;
}

.experience__skill-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
}

.experience__skill-header h4 {
    color: var(--color-white);
    font-size: 1rem;
    font-weight: 500;
    margin: 0;
}

.experience__skill-header small {
    font-size: 0.85rem;
}

.experience__progress-bar {
    width: 100%;
    height: 8px;
    background: rgba(77, 181, 255, 0.1);
    border-radius: 10px;
    overflow: hidden;
    position: relative;
}

.experience__progress-fill {
    height: 100%;
    background: var(--gradient-primary);
    border-radius: 10px;
    transition: width 1s ease;
    position: relative;
    overflow: hidden;
}

.experience__progress-fill::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(255, 255, 255, 0.3),
        transparent
    );
    animation: shimmer 2s infinite;
}

@keyframes shimmer {
    0% {
        transform: translateX(-100%);
    }
    100% {
        transform: translateX(100%);
    }
}


/* media query for medium devices */
@media screen and (max-width: 1024px) {
    .experience__container {
        grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }

    .experience__card {
        padding: 1.5rem;
    }
}

/* media query for small devices */
@media screen and (max-width: 600px) {
    .experience__container {
        grid-template-columns: 1fr;
        gap: 1.5rem;
    }

    .experience__card {
        padding: 1.5rem 1rem;
    }

    .experience__content {
        row-gap: 1.2rem;
    }
}